body{
	background: #fff;
}
.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.companyStyle{
	margin: 20px 0 0 8px;
	padding-top: 1px;
	padding-left: 10px;
	width: 180px;
	height: auto;
	color: #fff;
	cursor: pointer;
	float: left;
}
.companyStyle h2{
	font-size: 22px;
	line-height: 24px;
	word-break: break-all;
	padding-right: 10px;
	padding-bottom: 10px;
}
.marginZero{
	margin: 0 !important;
}
.right{
	float:right
}
.companyDetailsStyle{
	padding-top: 10px;
	padding-left: 20px;
	margin-top: 10px;
	border-top: 10px;
	font-size: 25px;
	width: 100%;
	height: 200px;
	bottom: 0;
	position: relative;
}